}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 140%;
  margin: 0 auto;
  height: 100%;
  color: #2D2D2D;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  height: auto;
}

a,
button {
  cursor: pointer;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
a:focus,
button:focus {
  outline: none !important;
}

h1 {
  font-family: "Amatic SC", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 75px;
  line-height: 100px;
  text-align: center;
}
