  }
}

h3 {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 44px;
  color: #2D2D2D;
}
@media (max-width: 1023px) {
  h3 {
    margin-bottom: 20px;
    font-size: 22px;
    line-height: 120%;
  }
}

h5 {
  font-family: "Montserrat Alternates", sans-serif;
  font-weight: 800;
  font-size: 20px;
  line-height: 130%;
  margin: 0;
}

h6 {
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
  line-height: 22px;
  margin: 0;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

p {
  margin: 0 0 20px 0;
}

.hidden {
  display: none i !important;
}

.show {
  display: block i !important;
}

