a {
  color: #2D2D2D;
  text-decoration: none;
  transition: all 0.3s;
}

input,
textarea {
  color: #f4f4f4;
  opacity: 1 !important;
}

table {
  width: 100%;
  border-collapse: collapse;
}

.container {
  width: 100%;
  max-width: 1430px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;
}
.container-wide {
  max-width: 100%;
}
@media (max-width: 1023px) {
  .container {
    max-width: 100%;
  }
}
@media (max-width: 767px) {
  .container {
    padding-right: 20px;
    padding-left: 20px;
  }
}
.container--wide {
  margin: 0 46px;
}

.v-select,
.vs__dropdown-toggle,
.v-select input {
  cursor: pointer !important;
}

#app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.error {
  border: 2px solid #DD3232 !important;
}

main {
  flex-grow: 1;
}

.grecaptcha-badge {
  display: none;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.scroll-off {
  overflow: hidden;
}

@keyframes move-gradient {
  from {
    background-position-x: left;
  }
  to {
    background-position-x: right;
  }
}
.slide-left-enter-active {
  transition: all 0.6s;
}

.slide-left-leave-active {
  transition: all 0.6s;
}

.slide-left-enter,
.slide-left-leave-to {
  transform: translateX(100%);
}

.fade-enter-active,
.fade-leave-active {
  transition-property: opacity;
  transition-duration: 0.3s;
}

.fade-enter-active {
  transition-delay: 0.3s;
}

.fade-enter,
.fade-leave-active {
  opacity: 0;
}

.header {
  min-height: 120px;
}

.header__top {
  display: flex;
  position: relative;
  align-items: center;
  min-height: 125px;
}

.header__menu {
  display: flex;
  margin: 0 auto;
  width: 94%;
}

.header__logo {
  position: absolute;
  content: "";
  top: 0;
  z-index: 2;
  left: -16%;
}

.header__menu-item {
  margin: 0 auto;
}
.header__menu-item a {
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  color: #2C7135;
  display: flex;
  align-items: flex-start;
}

.header__popup {
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
  color: #2C7135;
  text-align: center;
  position: absolute;
  width: 412px;
  height: 134px;
  right: 8%;
  top: 99px;
  background: #ffffff;
  border-radius: 15px;
  z-index: 3;
  padding: 27px 33px;
  box-shadow: 0px 8px 24px -7px rgba(44, 113, 53, 0.15);
}

.header__menu-svg {
  padding: 5px;
  width: 33px;
  height: 33px;
  border-radius: 50%;
  background-color: #C1F0C7;
  margin-left: 10px;
}
.header__menu-svg svg {
  display: flex;
}

.footer {
  background-color: #2C7135;
  min-height: 210px;
}

.footer__inner {
  display: flex;
  padding-top: 50px;
  align-items: center;
}
@media (max-width: 767px) {
  .footer__inner {
    flex-direction: column;
  }
}

.footer__text {
  color: #ffffff;
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: 15px;
  letter-spacing: 0em;
  text-align: left;
}

.form {
  background-color: #2D2D2D;
  min-height: 750px;
  padding: 130px 0;
}
@media (max-width: 1023px) {
  .form {
    padding: 50px 0;
    min-height: 450px;
  }
}
.form h3 {
  color: #ffffff;
  margin: 0;
}

.form__request {
  width: 76.7%;
}
@media (max-width: 1299px) {
  .form__request {
    width: 100%;
  }
}

.form__fields-wrapper {
  display: flex;
}
.form__fields-wrapper .form__field {
  width: 48.5%;
  margin-right: 3%;
}
.form__fields-wrapper .form__field:last-of-type {
  margin-right: 0;
}

.form__subtitle {
  margin-bottom: 49px;
}

.form__label {
  cursor: pointer;
  position: absolute;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 21px;
  top: 25px;
  left: 0;
  line-height: 100%;
  transition: 0.3s all;
  color: #f4f4f4;
}
@media (max-width: 1023px) {
  .form__label {
    font-size: 12px;
    line-height: 18px;
  }
}
.form__label--disabled span {
  opacity: 0.5;
}
.form__label h {
  opacity: 0;
  transition: 0.3s all;
}
.form__label:hover h {
  opacity: 1;
}

.form__field {
  position: relative;
  margin-bottom: 28px;
}
.form__field:last-of-type {
  margin-bottom: 30px;
}

.form__input,
.form__textarea {
  border-top: transparent;
  border-right: transparent;
  border-left: transparent;
  border-bottom: 2px solid #C4C4C4;
  background-color: #2D2D2D;
  padding: 23px 15px 0;
  font-weight: normal;
  font-size: 14px;
  line-height: 21px;
  transition: 0.3s all;
  color: #ffffff;
}
@media (max-width: 1023px) {
  .form__input,
.form__textarea {
    font-size: 12px;
    line-height: 18px;
  }
}
.form__input:hover,
.form__textarea:hover {
  border-top: transparent;
  border-right: transparent;
  border-left: transparent;
  border-bottom: 2px solid #F6F6F6;
}
.form__input:focus,
.form__textarea:focus {
  border-top: transparent;
  border-right: transparent;
  border-left: transparent;
  border-bottom: 2px solid #F6F6F6;
  padding: 26px 15px 8px;
}
.form__input--error,
.form__textarea--error {
  border-radius: 10px;
  border: 1px solid #DD3232 !important;
}

.form__input:focus + label,
.form__textarea:focus + label,
.form__input:valid + label,
.form__textarea:valid + label,
.form__input--filled + label,
.form__textarea--filled + label {
  top: 8px;
  font-size: 13px;
}
.form__input:focus + label h,
.form__textarea:focus + label h,
.form__input:valid + label h,
.form__textarea:valid + label h,
.form__input--filled + label h,
.form__textarea--filled + label h {
  opacity: 1;
}

.form__input:hover + label h {
  opacity: 1;
}

.form__input {
  width: 100%;
  height: 52px;
  margin-bottom: 10px;
}

.form__textarea {
  height: 52px;
  min-height: 52px;
  max-width: 100%;
  width: 100%;
  line-height: 15px;
  white-space: nowrap;
  resize: none;
}

.icon-error {
  position: absolute;
  top: 14px;
  right: 15px;
  width: 24px;
  height: 24px;
  z-index: 1;
}

.form__error {
  position: absolute;
  top: 0;
  left: calc(100% - 27px);
  font-size: 9px;
  line-height: 100%;
  padding: 4px 6px;
  background: #FFFFFF;
  opacity: 0;
  z-index: 0;
  transition: 0.3s all;
}

.icon-error:hover + .form__error,
.form__error:hover {
  opacity: 0.8;
  z-index: 1;
}

.form__length {
  position: absolute;
  font-size: 13px;
  line-height: 100%;
  opacity: 0.5;
  bottom: 32px;
  right: 15px;
}

.form__agree {
  text-align: center;
  font-style: normal;
