.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  outline: none !important;
  border: none;
  transition: 0.3s all;
  cursor: pointer;
  flex-shrink: 0;
}
.button--toggle {
  position: absolute;
  width: 30px;
  height: 33px;
  display: flex;
  background-color: #FFFFFF;
  align-items: center;
  justify-content: center;
  transition: 0.3s all;
  right: 25px;
  top: 30px;
  padding: 0;
  z-index: 2;
}
.button--toggle:before {
  content: "";
  position: absolute;
  width: 5px;
  height: 30px;
  background-color: #2C7135;
  transform: rotate(45deg);
  transition: 0.3s all;
}
.button--toggle:after {
  content: "";
  position: absolute;
  width: 5px;
  height: 30px;
  background-color: #2C7135;
  transform: rotate(-45deg);
  transition: 0.3s all;
}
.button--toggle.close {
  background-color: #FFFFFF;
}
.button--toggle.close span {
  background-color: #2C7135;
  height: 5px;
  width: 30px;
}
.button--toggle.close:before {
  height: 30px;
  top: -6px;
  background-color: #2C7135;
  transform: rotate(90deg);
}
.button--toggle.close:after {
  height: 30px;
  bottom: -6px;
  background-color: #2C7135;
  transform: rotate(90deg);
}
@media (min-width: 1023px) {
  .button--toggle {
    display: none;
  }
}
@media (max-width: 1023px) {
  .button--toggle {
    display: flex;
  }
}
.button--round {
  border-radius: 50%;
  background-color: #2D2D2D;
  display: block;
  width: 120px;
  min-height: 120px;
  padding: 48px 0;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  height: 24px;
  /* fallback */
  margin: 0;
  color: #ffffff;
  font-style: normal;
  font-weight: bold;
  font-size: 10px;
  line-height: 12px;
  text-transform: uppercase;
  text-align: center;
}
.button--white {
  background-color: #ffffff;
  font-style: normal;
  font-weight: bold;
  font-size: 10px;
  line-height: 12px;
  height: 12px;
  text-align: center;
  text-transform: uppercase;
  color: #2D2D2D;
  display: block;
  padding: 48px 5px;
  margin-right: auto;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: center;
}
.button--more {
  border: 1px solid #2D2D2D;
}
@media (min-width: 1024px) {
  .button--more {
    transform: translateY(37px);
  }
}
@media (max-width: 1023px) {
  .button--more {
    margin: 0 auto;
  }
}
.button .icon {
  stroke: #FFFFFF;
  transition: 0.3s all;
}

